<template>
<div>
  <el-container style='height:100%'>
    <el-aside width="300px">
        <common-aside></common-aside>      
    </el-aside>
    <el-container>
      <el-header height="140px">
        <!-- <h3>一柒</h3> -->
        <common-header></common-header>
      </el-header>
      <el-main>
        <!-- <keep-alive> -->
          <router-view></router-view>        
        <!-- </keep-alive> -->
      </el-main>
    </el-container>
  </el-container>  
  <div id="progress" @click="backTop()">
    <el-progress type="circle" :percentage="percent|| 0" :width="80"></el-progress>    
  </div>

</div>
</template>

<script>
import CommonAside from './Aside'
import CommonHeader from './Header/header.vue'
export default {
  components:{
    CommonAside,
    CommonHeader
  },
  data() {
    return {
      percent:''
    }
  },
  methods: {
    scrolling(){
      let allHeight=document.documentElement.scrollHeight
      let seeHeight=document.documentElement.clientHeight
      let nowPosition = window.scrollY
      let all = allHeight-seeHeight
      let nowPercent = nowPosition/all
      let npercent = Math.floor(nowPercent*100)
      this.percent = npercent
      console.log(allHeight)
    }, 
    backTop(){
      document.documentElement.scrollTop = document.body.scrollTop =0
    }
  },
  mounted() {
    // window.addEventListener('scroll',this.scrolling)
    window.addEventListener('scroll',this.scrolling,true)
  },
}

// function lazy(){
